import React from 'react';
import Carousel from 'react-bootstrap/Carousel';

export default class ImageCarrosel extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			images: [
				"data:image/webp;base64,",
				"data:image/jpeg;base64,"
			]
		};
	}
	render() {
		return (
			<Carousel className='carousel' fade>
				{
					this.state.images.map((image, index) => {
						return (
							<Carousel.Item interval={5000} key={`slide ${index}`}>
								<img
									className="d-block w-100"
									src={image}
									alt={`Slide ${index + 1}`}
								/>
							</Carousel.Item>
						)
					})
				}
			</Carousel>
		)
	}
}